<template>
  <div class="home">
    <vue-header>
      <template #left v-if="camerashow">
        <img class="camera" src="../img/camera.svg" alt="" @click="toUpload"/>
      </template>
      <template #right v-if="exitshow">
        <img class="exit" src="../img/exit.svg" alt="" @click="exit">
      </template>
    </vue-header>
    <keep-alive include="upload">
      <router-view></router-view>
    </keep-alive>
    <vue-footer></vue-footer>
  </div>
</template>

<script>
import vuegram from "@/components/header.vue";
import vuefooter from "@/components/footer.vue";

export default {
  name: "Home",
  data() {
    return {
      camerashow: false,
      exitshow: false,
    }
  },
  components: {
    "vue-header": vuegram,
    "vue-footer": vuefooter,
  },
  methods: {
    toUpload() {
      let item
      this.$children.forEach(element => {
        if(element.dropfn) {
          item = element
        }
      })
      item.$refs.gg.click()
    },
    swslot(name) {
      if(name == 'upload') {
        this.camerashow = true
        this.exitshow = false
      }
      else if(name == 'profile') {
        this.exitshow = true
        this.camerashow = false
      }
      else {
        this.camerashow = false
        this.exitshow = false
      }
    },
    exit() {
      sessionStorage.clear()
      this.$store.commit('setUser')
      this.$router.replace({
        path: "/",
      });
    }
  },
};
</script>

<style scoped>
.home {
  width: 100%;
  height: 100%;
  background-color: #fafafa;
  /* border: 0.5px solid #444444; */
  position: relative;
}

.camera {
  width: 25px;
  height: 25px;
  position: absolute;
  left: 5%;
  top: 4px;
  cursor: pointer;
}

.exit {
  width: 25px;
  height: 25px;
  position: absolute;
  left: 86%;
  top: 4px;
  cursor: pointer;
}
</style>