<template>
  <footer>
    <ul>
      <li>
        <router-link to="subscribe">
          <img name="home" src="../img/home.svg" @click="ac" alt=""/>
        </router-link>
      </li>
      <li>
        <router-link to="search">
          <img name="search" src="../img/search.svg" @click="ac" alt=""/>
        </router-link>
      </li>
      <li>
        <router-link to="upload">
          <img name="upload" src="../img/upload.svg" @click="ac" alt=""/>
        </router-link>
      </li>
      <li>
        <router-link to="profile">
          <img name="profile" src="../img/profile.svg" @click="ac" alt=""/>
        </router-link>
      </li>
    </ul>
  </footer>
</template>

<script>
export default {
    name: 'Footer',
    methods: {
      ac(e) {
        if(e.target.name == "profile") {
          this.$parent.swslot(e.target.name)
        }
        else if(e.target.name == "upload") {
          this.$parent.swslot(e.target.name)
        }
        else {
          this.$parent.swslot(e.target.name)
        }
      }
    }
};
</script>

<style scoped>
footer {
  position: relative;
  width: 100%;
  bottom: 0px;
  height: 35px;
  /* border-top: 0.5px solid #444444; */
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0px 8px;
  overflow: hidden;
}

ul {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  overflow: hidden;
}

li {
  list-style: none;
  float: left;
  margin: 0;
  padding: 0;
}

img {
  width: 23px;
  height: 23px;
  padding: 6px;
  cursor: pointer;
}

